import api from '../../api/index';

export default {
  namespaced: true,
  state: {
    auth: localStorage.getItem('auth') || '',
    me: JSON.parse(localStorage.getItem('me')),
  },
  getters: {
    isLoggedIn: ({ auth }) => !!auth,
  },
  actions: {
    async register({ commit }, data) {
      const res = await api.register(data.login, data.password);

      if (res.status !== 'ok') {
        throw new Error(`Error: ${res.data[0].detail}`);
      }

      const token = btoa(`${data.login}:${data.password}`);

      commit('setAuth', token);

      localStorage.setItem('auth', token);
    },
    async login({ commit }, data) {
      const res = await api.signin(data.login, data.password);

      if (res.status !== 'ok') {
        throw new Error(`Error: ${res.data[0].detail}`);
      }

      const token = btoa(`${data.login}:${data.password}`);

      commit('setAuth', token);

      localStorage.setItem('auth', token);
    },
    async logout({ commit }) {
      localStorage.removeItem('auth');
      localStorage.removeItem('me');

      commit('removeAuth');
      commit('removeMe');
    },
    async getMe({ commit, state }) {
      const res = await api.getMe(state.auth);

      if (res.status !== 'ok') {
        throw new Error(`Error: ${res.data[0].detail}`);
      }

      const me = res.data;

      commit('setMe', me);

      localStorage.setItem('me', JSON.stringify(me));
    },
  },
  mutations: {
    removeAuth(state) {
      state.auth = null;
    },
    setAuth(state, auth) {
      state.auth = auth;
    },
    removeMe(state) {
      state.me = null;
    },
    setMe(state, me) {
      state.me = me;
    },
  },
};
