const baseUrl = 'https://money-staging-jit.herokuapp.com';

const register = (login, password) => fetch(`${baseUrl}/signup`, {
  method: 'POST',
  headers: {
    Accept: '*/*',
    'Content-Type': 'application/json',
  },
  body: JSON.stringify({ login, password }),
}).then((res) => res.json());

const signin = (login, password) => fetch(`${baseUrl}/signin`, {
  method: 'POST',
  headers: {
    Accept: '*/*',
    'Content-Type': 'application/json',
  },
  body: JSON.stringify({ login, password }),
}).then((res) => res.json());

const removeMe = (token) => fetch(`${baseUrl}/removeMe`, {
  method: 'DELETE',
  headers: {
    Accept: '*/*',
    'Content-Type': 'application/json',
    Authorization: `Basic ${token}`,
  },
}).then((res) => res.json());

const getMe = (token) => fetch(`${baseUrl}/me`, {
  method: 'GET',
  headers: {
    Accept: '*/*',
    'Content-Type': 'application/json',
    Authorization: `Basic ${token}`,
  },
}).then((res) => res.json());

export default {
  register,
  removeMe,
  signin,
  getMe,
};
